import styled from "styled-components";

export const StyledButton = styled.input`
    min-width:60px;
    min-height:30px;
    font-size: 15px;
    display:inline-block;
    padding:0.2em 1.45em;
    margin:0.1em;
    border:0.15em solid ${props => props.theme.colorPalettes.second.other};
    color:white;
    background-color:${props => props.theme.colorPalettes.second.other};
    text-align:center;
    cursor:pointer;
    font-weight:500;
    font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, "Helvetica Neue";
    &:hover {
        border-color:#4c8da1;
        background-color:#3b7b8f;
    }
    &:active {
        background-color:#51737d;
        outline: 0;
    }
    &:focus {
    background-color:#51737d;
        outline: 0;
    }
    @media all and (max-width:30em){
        display:block;
        margin:0.2em auto;
    }
    border-radius: ${props => props.theme.borderRadius||"0px"};
    @media ${props => props.theme.media.phone}{
            margin:0px;
    }
`


//2
const StyledButton2 = styled.input`
    min-width: 80px;
    min-height: 40px;
    background: #f3f0f1;
    position: relative;
    text-align: center;
    cursor: pointer;
    transition: all 0.1s ease-in-out;
   
    box-shadow: -6px -6px 10px rgba(255, 255, 255, 0.8),
                 6px 6px 10px rgba(0, 0, 0, 0.2);
               
    &:hover {
        opacity: 0.7;
        box-shadow: -6px -6px 10px rgba(255, 255, 255, 0.8),
          6px 6px 10px rgba(0, 0, 0, 0.2);
          
    }
   
    &:active {
        opacity: 1;
        box-shadow: inset -4px -4px 8px rgba(255, 255, 255, 0.5),
          inset 8px 8px 16px rgba(0, 0, 0, 0.1);
        color: #79e3b6;
        outline: 0;
    }
    &:focus {
        box-shadow: inset -4px -4px 8px rgba(255, 255, 255, 0.5),
          inset 8px 8px 16px rgba(0, 0, 0, 0.1);
        outline: 0;
    }
`